// 640px, 900px, 1400px
$breakpoints-up: (
  "medium": "40em",
  "large": "65.25em",
  "xlarge": "87.5em",
);

// 639px, 1149px, 1399px
$breakpoints-down: (
  "small": "39.9375em",
  "medium": "71.8125em",
  "large": "87.4375em",
);

@mixin bp($size) {
  @media (min-width: map-get($breakpoints-up, $size)) {
    @content;
  }
}

@mixin bp-down($size) {
  @media (max-width: map-get($breakpoints-down, $size)) {
    @content;
  }
}
